var initialExample =
`
render: function(){
  var Multiselect = require('react-widgets').Multiselect
    , list = [
      { label: 'orange', id: 1 },
      { label: 'blue', id: 2 },
      { label: 'red', id: 3 },
    ];
 
  return (
    <Multiselect 
      data={list}
      value={this.state.value}
      onChange={this._change}
      textField='label'
      valueField='id'/>
   )
}`;

var onCreateExample =
`
render(){
  var list = [
      { label: 'orange', id: 1 },
      { label: 'blue', id: 2 },
      { label: 'red', id: 3 },
    ];
  
  function change(values){
    this.setState({ value: values })
  }

  function create(color){
    var tag = { label: color, id: list.length + 1 } // create a tag object

    list.push(value) // add new tag to the data list
    this.setState({ value: this.state.value.concat(tag) }) //add new tag tothe list of values
  }

  return (
    <Multiselect 
      value={this.state.value} 
      data={data} 
      textfield="label"
      onCreate={create} 
      onChange={change}/>
  )
}`;

module.exports = Multiselect;


