'use strict';
var React = require('react');

var defaultValue = React.createClass({displayName: 'defaultValue',

  render: function() {
    return (
      React.createElement("span", {className: "default"}, 
        "(default: " + this.props.children + ")"
      )
    );
  }

});

module.exports = defaultValue;


